import http from 'src/services/network';
import { storeContactsContent, storePage } from 'src/services/pages/actions';


const fetchPage = (pageName) => {
  const url = `${API_URL}/pages/?name=${pageName}`;

  return http.get(url)
    .then(response => response);
};

export const fetchAndStorePage = pageName => dispatch => fetchPage(pageName)
  .then((response) => {
    dispatch(storePage(response.data));
  });

export const fetchAndStoreContactsContent = () => dispatch => fetchPage('Contacts')
  .then((response) => {
    dispatch(storeContactsContent(response.data[0].content || ''));
  });
