import { createSelector } from 'reselect';
import { pageButtonsSelector } from 'src/services/pages/selectors';


const optionsSelector = state => state.options;

export const linksSelector = createSelector(
  [optionsSelector],
  (options) => {
    const linksObject = options.find(e => e.name === 'links');

    if (linksObject) return linksObject.items;
    return [];
  },
);

export const headerLinksSelector = createSelector(
  [linksSelector],
  links => [...links]
    .filter(link => link.enabled)
    .sort((prev, next) => (prev.priority < next.priority ? -1 : 1)),
);

export const sideNavLinksSelector = createSelector(
  [linksSelector, pageButtonsSelector],
  (links, pageButtons) => [...links, ...pageButtons]
    .filter(link => link.enabled)
    .sort((prev, next) => (prev.priority < next.priority ? -1 : 1)),
);

export const sideNavSocLinksSelector = createSelector(
  [optionsSelector],
  options => options.filter(option => option.name === 'facebook_url' || option.name === 'twitter_url'),
);

export const supportNumberSelector = createSelector(
  [optionsSelector],
  (options) => {
    const obj = options.find(e => e.name === 'support_number');

    if (obj) return obj.value;
    return '';
  },
);

export const appUrlSelector = createSelector(
  [optionsSelector],
  (options) => {
    const obj = options.find(e => e.name === 'android_app_url');

    if (obj) return obj.value;
    return '';
  },
);
