import { STORE_CONTACTS_CONTENT, STORE_PAGE_CONFIG } from 'src/services/pages/types';


const initialState = {
  pageConfig: {
    buttons: [],
  },
  contactsContent: '',
};


const reducer = (state = initialState, action) => {
  switch (action.type) {
    case STORE_PAGE_CONFIG:
      return {
        ...state,
        pageConfig: action.data[0] || {},
      };
    case STORE_CONTACTS_CONTENT:
      return {
        ...state,
        contactsContent: action.data,
      };
    default:
      return state;
  }
};


export default reducer;
