import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import {
  appUrlSelector,
  headerLinksSelector,
  supportNumberSelector,
} from 'src/services/options/selectors';
import { connect } from 'react-redux';
import { Logo } from 'src/components/Logo';
import SideNav from 'src/components/SideNav';
import {
  Chat,
  ChatBtn,
  ChatText,
  HeaderContent,
  HeaderContentTop,
  HeaderLink,
  HeaderLinks,
  PopupBtn,
  PopupBtnIcon,
  PopupBtnText,
  PopupBtnWrap,
  PopupContent,
  RoundBtn,
} from 'src/components/Header/styled';
import showdown from 'showdown';
import { contactsContentSelector } from 'src/services/pages/selectors';
import { Desktop, Mobile } from 'src/components/Utils/Media';


class Header extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isContactsOpen: false,
      popupTop: 0,
    };

    this.contactsRef = React.createRef();
  }

  toggleContactsPopup = () => {
    const contacts = this.contactsRef.current;
    const top = contacts.getBoundingClientRect().top + contacts.clientHeight + 10;

    this.setState(state => ({
      isContactsOpen: !state.isContactsOpen,
      popupTop: top,
    }));
  };

  renderContactLink = () => {
    const { links, contactsContent, supportNumber } = this.props;
    const { isContactsOpen, popupTop } = this.state;

    const link = links.find(l => l.name === 'Contacts');
    if (!link) return <div />;

    const converter = new showdown.Converter();
    const html = converter.makeHtml(contactsContent);
    return (
      <PopupBtnWrap ref={this.contactsRef}>
        <Desktop>
          <PopupBtnText>{supportNumber}</PopupBtnText>
        </Desktop>
        <PopupBtn>
          <PopupBtnIcon
            onClick={this.toggleContactsPopup}
            direction={isContactsOpen ? 'up' : 'down'}
          />
          <Link to={link.target}>{link.label}</Link>
          <PopupContent
            visible={isContactsOpen}
            top={popupTop}
            dangerouslySetInnerHTML={{ __html: html }}
          />
        </PopupBtn>
      </PopupBtnWrap>
    );
  };

  renderMainLinks = () => {
    const { links } = this.props;

    const mainLinks = links
      .filter(link => link.name !== 'Contacts')
      .map((link) => {
        const props = { key: link.id };

        // local link
        if (link.target) {
          props.to = link.target;
          props.as = Link;
          // external link
        } else {
          props.href = link.url;
          props.target = '_blank';
        }

        return <HeaderLink {...props}>{link.label}</HeaderLink>;
      });

    return <HeaderLinks>{mainLinks}</HeaderLinks>;
  };

  render() {
    const { appUrl } = this.props;

    return (
      <header>
        <Mobile>
          <HeaderContentTop>
            <SideNav />
            <Logo to="/" />
            <RoundBtn href={appUrl} target="_blank">GET APP</RoundBtn>
          </HeaderContentTop>

          <HeaderContent>
            {this.renderMainLinks()}
            {this.renderContactLink()}
            <ChatBtn />
          </HeaderContent>
        </Mobile>

        <Desktop>
          <SideNav />
          <HeaderContentTop>
            {this.renderMainLinks()}
            <Chat>
              <ChatBtn />
              <ChatText>Chat online</ChatText>
            </Chat>
            {this.renderContactLink()}
            <RoundBtn href={appUrl} target="_blank">GET APP</RoundBtn>
          </HeaderContentTop>
        </Desktop>
      </header>
    );
  }
}

Header.propTypes = {
  links: PropTypes.arrayOf(PropTypes.object).isRequired,
  contactsContent: PropTypes.string.isRequired,
  supportNumber: PropTypes.string.isRequired,
  appUrl: PropTypes.string.isRequired,
};

const mapStateToProps = state => ({
  links: headerLinksSelector(state),
  contactsContent: contactsContentSelector(state),
  supportNumber: supportNumberSelector(state),
  appUrl: appUrlSelector(state),
});

export default connect(mapStateToProps)(Header);
