import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Header from 'src/components/Header';
import About from 'src/components/AboutPage';
import Home from 'src/components/HomePage';
import React from 'react';
import { fetchOptions } from 'src/services/options/thunks';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { fetchAndStoreContactsContent, fetchAndStorePage } from 'src/services/pages/thunks';


class Routes extends React.Component {
  componentDidMount() {
    this.props.fetchOptions();
    this.props.fetchHomePage();
    this.props.fetchContactsContent();
  }

  render() {
    return (
      <Router>
        <Header />
        <Switch>
          <Route path="/about" component={About} />
          <Route path="/" component={Home} />
        </Switch>
      </Router>
    );
  }
}

Routes.propTypes = {
  fetchOptions: PropTypes.func.isRequired,
  fetchHomePage: PropTypes.func.isRequired,
  fetchContactsContent: PropTypes.func.isRequired,
};

const mapDispatchToProps = dispatch => ({
  fetchOptions: () => dispatch(fetchOptions()),
  fetchHomePage: () => dispatch(fetchAndStorePage('Main')),
  fetchContactsContent: () => dispatch(fetchAndStoreContactsContent()),
});

export default connect(null, mapDispatchToProps)(Routes);
