import React from 'react';
import PropTypes from 'prop-types';
import { IconClose } from 'src/components/Icons/IconClose';
import { IconArrow } from 'src/components/Icons/IconArrow';
import { IconFacebook } from 'src/components/Icons/IconFacebook';
import { IconTwitter } from 'src/components/Icons/IconTwitter';
import {
  SideNavBtn, SideNavContent, SideNavHeader,
  SideNavLink, SideNavLinks, SideNavLogo, SideNavOverlay,
  SideNavSocLink, SideNavSocLinks, SideNavWrap,
} from 'src/components/SideNav/styled';
import { sideNavLinksSelector, sideNavSocLinksSelector } from 'src/services/options/selectors';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { Desktop, Mobile } from 'src/components/Utils/Media';
import { Logo } from 'src/components/Logo';
import { IconMenu } from 'src/components/Icons/IconMenu';


class SideNav extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isOpen: false,
    };
  }

  toggleNav = () => {
    this.setState(state => ({ isOpen: !state.isOpen }));
  };

  renderMainLinks = () => {
    const { mainLinks } = this.props;

    const links = mainLinks.map((link) => {
      const props = {
        key: link.id,
      };

      // internal links
      if (link.target && link.type !== 'Check') {
        props.as = Link;
        props.to = link.target;
        // modal links
      } else if (link.target && link.type === 'Check') {
        props.as = 'span';
        // external links
      } else {
        props.href = link.url;
        props.target = '_blank';
      }

      return (
        <SideNavLink {...props}>
          {link.label}
          <IconArrow />
        </SideNavLink>
      );
    });

    return <SideNavLinks>{links}</SideNavLinks>;
  };

  renderSocLinks = () => {
    const { socLinks } = this.props;
    const { isOpen } = this.state;

    const links = socLinks.map((link) => {
      const props = {
        key: link.id,
        href: link.value,
        target: '_blank',
      };
      let icon;

      switch (link.name) {
        case 'facebook_url':
          icon = <IconFacebook />;
          break;
        case 'twitter_url':
          icon = <IconTwitter />;
          break;
        default:
          icon = null;
      }

      return <SideNavSocLink {...props}>{icon}</SideNavSocLink>;
    });

    return <SideNavSocLinks isOpen={isOpen}>{links}</SideNavSocLinks>;
  };

  render() {
    const { isOpen } = this.state;

    return (
      <SideNavWrap isOpen={isOpen}>
        <Mobile>
          <SideNavOverlay onClick={this.toggleNav} isOpen={isOpen} />
          <SideNavBtn as={IconMenu} onClick={this.toggleNav} />

          <SideNavContent isOpen={isOpen}>

            <SideNavHeader>
              <IconClose onClick={this.toggleNav} />
              <SideNavLogo to="/" />
            </SideNavHeader>

            {this.renderMainLinks()}
            {this.renderSocLinks()}

          </SideNavContent>

        </Mobile>

        <Desktop>
          <Logo to="/" />
          <SideNavBtn isOpen={isOpen} onClick={this.toggleNav}>
            <span />
          </SideNavBtn>

          <SideNavContent isOpen={isOpen}>
            {this.renderMainLinks()}
          </SideNavContent>
          {this.renderSocLinks()}
        </Desktop>
      </SideNavWrap>
    );
  }
}

SideNav.propTypes = {
  mainLinks: PropTypes.arrayOf(PropTypes.object).isRequired,
  socLinks: PropTypes.arrayOf(PropTypes.object).isRequired,
};

const mapStateToProps = state => ({
  mainLinks: sideNavLinksSelector(state),
  socLinks: sideNavSocLinksSelector(state),
});

export default connect(mapStateToProps)(SideNav);
